import { Typography, Row, Tooltip, Layout } from "antd";
// @ts-ignore
import { FpsView } from "react-fps";
import {
  PoweroffOutlined,
  PauseOutlined,
  RedoOutlined,
  SearchOutlined,
  CloseOutlined,
  EyeOutlined,
  RobotOutlined,
} from "@ant-design/icons";

import * as S from "./game-template.styled";
import { useState } from "react";
import { useStore } from "effector-react";
import { $snakes } from "../../../game";

const { Content } = Layout;

export const GameTemplate = ({
  children,
}: {
  children: React.ReactElement;
}) => {
  const [isVisible, setVisible] = useState(true);

  const snakes = useStore($snakes);

  const snakesInGame = snakes.filter((s) => !s.isCrash);

  const toggleVisible = () => {
    setVisible((s) => !s);
  };

  return (
    <div style={{ height: "100vh", position: "relative" }}>
      {isVisible && (
        <S.FpsWrapper>
          <FpsView top="calc(100% - 80px)" height={0} />
        </S.FpsWrapper>
      )}

      <Layout style={{ height: "100%" }}>
        <S.Header>
          <Row>
            <Typography.Title level={5}></Typography.Title>
            <Typography.Title level={5}> </Typography.Title>
            <Typography.Title level={5}></Typography.Title>
          </Row>

          <Row align="middle">
            <EyeOutlined />
            <Tooltip placement="bottom" title=" ">
              <Typography.Title level={5} onClick={toggleVisible}>
                {isVisible ? "" : ""}
              </Typography.Title>
            </Tooltip>
          </Row>
        </S.Header>

        <Layout hasSider>
          <Content>{children}</Content>

          <S.Wrapper isVisible={isVisible}>
            <Row>
              <Tooltip placement="top" title="">
                <CloseOutlined className="close" />
              </Tooltip>
            </Row>

            <Row align="middle">
              <RobotOutlined className="icon" />
              <Typography.Text>
                : {snakes.length} - , {snakesInGame.length} -  
              </Typography.Text>
            </Row>
          </S.Wrapper>
        </Layout>

        <S.Footer isVisible={isVisible}>
          <Row align="middle">
            <Row align="middle">
              <PauseOutlined />
              <Typography.Title level={5}></Typography.Title>
            </Row>

            <Row align="middle">
              <RedoOutlined />
              <Typography.Title level={5}></Typography.Title>
            </Row>

            <Row align="middle">
              <PoweroffOutlined />
              <Typography.Title level={5}> </Typography.Title>
            </Row>

            <Row align="middle">
              <SearchOutlined />
              <Typography.Title level={5}> </Typography.Title>
            </Row>
          </Row>
        </S.Footer>
      </Layout>
    </div>
  );
};
